body {
    background: #faf8ef;
}

.container-fluid {
    margin-top: 40px;
    max-width: 580px;
    padding-bottom: 60px;
    margin: 0 auto;
}
.title {
    font-family: Montserrat, sans-serif;
    font-size: 36px;
    margin-top: 60px;
    margin-bottom: 12px;
    text-align: center;
    width: 100%;
    color: #444;
    font-weight: 700;
}

.flex-row {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    line-height: 30px;
}

.piece:before {
    content: '';
    float: left;
    padding-top: 100%;
}

.piece {
    background-color: rgb(231, 222, 209);
    cursor: pointer;
    margin: 1.5px;
    font-weight: bold;
    text-align: center;
    flex: 1 0 auto;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
