body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background: #faf8ef;
}

.container-fluid {
    margin-top: 40px;
    max-width: 580px;
    padding-bottom: 60px;
    margin: 0 auto;
}
.title {
    font-family: Montserrat, sans-serif;
    font-size: 36px;
    margin-top: 60px;
    margin-bottom: 12px;
    text-align: center;
    width: 100%;
    color: #444;
    font-weight: 700;
}

.flex-row {
    padding: 0;
    margin: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    line-height: 30px;
}

.piece:before {
    content: '';
    float: left;
    padding-top: 100%;
}

.piece {
    background-color: rgb(231, 222, 209);
    cursor: pointer;
    margin: 1.5px;
    font-weight: bold;
    text-align: center;
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

